import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

import { PresentationAmortizationPaymentStatus } from '@backbase/data-ang/loans';

import {
  LoanPaymentScheduleStatusColorMap,
  loanPaymentScheduleStatusColorMap,
} from './loan-payment-schedule-status.model';

@Component({
  selector: 'bb-loan-payment-schedule-status',
  templateUrl: './loan-payment-schedule-status.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanPaymentScheduleStatusComponent {
  /**
   * The loan item status
   */
  @Input() status!: PresentationAmortizationPaymentStatus;
  /**
   * Whether the status should be truncated
   *
   * @default: false
   */
  @Input() truncate = false;
  /**
   * Map of colors for each status
   */
  @Input() statusColorMap: LoanPaymentScheduleStatusColorMap = loanPaymentScheduleStatusColorMap;
}
